/*
*axios
*/
import axios from 'axios'

let baseURL
console.log(process.env.NODE_ENV, process.env.NODE_TEST)
if (process.env.NODE_ENV === 'development') {
  // 
  baseURL = 'https://api.github.com'
} else {
  // 
  if (process.env.NODE_TEST === 'test') {
    // 
    baseURL = 'http://test.xxx.com/'
  } else {
    // 
    baseURL = 'https://api.github.com'
  }
}

let ajax = data => {
  /*
  
  */
  axios.interceptors.request.use(
    config => {
      return config
    },
    error => {
      return Promise.reject(error)
    }
  )
  /*
  
  */
  axios.interceptors.response.use(
    res => {
      /*
    github_api
    */

      return res
    },
    error => {
      return Promise.reject(error)
    }
  )

  let params = {
    ...data,
    baseURL: baseURL,
    timeout: 2000,
    responsetype: 'json',
    validateStatus: status => {
      if (status < 200 || status >= 300) {
        console.log('-_-!')
      }
      return status >= 200 && status < 300
    }
  }

  return axios(params)
}

export default ajax



